$(document).ready(function () {
	$('.form-signin').validate({
		rules: {
			firstname: {
				required: true
			},
			lastname: {
				required: true
			},
			emailaddress: {
				required: true,
				email: true
			},
			password: {
				minlength: 6,
				required: true
			},
			confirmation: {
				minlength: 6,
				equalTo: "#password"
			}
		},
		success: function (element) {
			element
			.text('OK!').addClass('valid')
		}
	});
});